import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { FilePath } from '@ionic-native/file-path';
import { Camera } from '@ionic-native/camera';

import { MyApp } from './app.component';
import { CustomAlertPage } from '../pages/custom-alert/custom-alert';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { WebsiteProvider } from '../providers/website/website';
import { DatabaseProvider } from '../providers/database/database';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@NgModule({
  declarations: [
    MyApp,
    CustomAlertPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/admin/admin.module.ngfactory#AdminPageModuleNgFactory', name: 'AdminPage', segment: 'admin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/adminlogin/adminlogin.module.ngfactory#AdminloginPageModuleNgFactory', name: 'AdminloginPage', segment: 'adminlogin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cart/cart.module.ngfactory#CartPageModuleNgFactory', name: 'CartPage', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/category/category.module.ngfactory#CategoryPageModuleNgFactory', name: 'CategoryPage', segment: 'category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/productdetail/productdetail.module.ngfactory#ProductdetailPageModuleNgFactory', name: 'ProductdetailPage', segment: 'productdetail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products/products.module.ngfactory#ProductsPageModuleNgFactory', name: 'ProductsPage', segment: 'products', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    CustomAlertPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    WebsiteProvider,
    DatabaseProvider,
    InAppBrowser,
    PhotoViewer,
    FileTransfer,
    // FileUploadOptions,
    FileTransferObject,
    File,
    FilePath,
    Camera
  ]
})
export class AppModule {}



